import { graphql, useStaticQuery } from "gatsby"
import { GatsbyImage, getImage, StaticImage, withArtDirection } from "gatsby-plugin-image"
import React, { useRef, useState } from "react"
import useOutsideClick from "../../hooks/useOutsideClick"
import {Link, useTranslation} from 'gatsby-plugin-react-i18next';

export function navItems(t) {
  return [
    [t('services'), '/service/systems'],
    [t('works'), '/works'],
    [t('career'), '/career'],
    [t('blog'), '/blog'],
  ]
};

function mainNavItems(t) {
  return [
    ...navItems(t),
    [t('request for quotation'), null],
  ]
};

const CustomLink = ({
  activeClassName,
  className,
  children,
  to,
}) => {
  if (to) {
    return (
      <Link className={className} activeClassName={activeClassName} to={to}>
        {children}
      </Link>
    )
  }

  function onClickRequest() {
    if (!document) {
      return;
    }
    document!.querySelector('.form-section')!.scrollIntoView({ behavior: 'smooth' });
  }

  return (
    <button className={className} onClick={onClickRequest}>
      {children}
    </button>
  )
}

const MobileNav = ({
  forwardRef,
  visible,
}) => {
  const mobileNavClasses = ["bg-primary flex flex-col basis-full self-center py-7 space-y-8 justify-end"];
  const {t} = useTranslation()

  if (!visible) {
    mobileNavClasses.push("hidden");
  }

  return (
    <nav ref={forwardRef} className="absolute left-0 right-0 z-10">
      <div className={mobileNavClasses.join(' ')}>
        {mainNavItems(t).map(([title, url]) => (
          <CustomLink
            key={`mob-nav-${url}`}
            className="text-white text-center hover:text-secondary text-base"
            activeClassName=""
            to={url}
          >
            {title}
          </CustomLink>
        ))}
      </div>
    </nav>
  )
}

const DesktopNav = () => {
  const {t} = useTranslation()
  return (
    <div className="hidden flex-row self-center space-x-8 justify-end lg:flex">
      {mainNavItems(t).map(([title, url]) => (
        <CustomLink
          key={`nav-${url}`}
          className="font-bold text-primary hover:text-secondary xl:text-xl"
          activeClassName="text-secondary"
          to={url}
        >
          {title}
        </CustomLink>
      ))}
    </div>
  )
}

const Navbar = ({ logoOnly }: { logoOnly?: boolean }) => {
  const { strapiGlobal } = useStaticQuery(graphql`
    query {
      strapiGlobal {
        logo {
          localFile {
            childImageSharp {
              gatsbyImageData(
                width: 53
                placeholder: TRACED_SVG
              )
            }
          }
        }
        logoFull {
          localFile {
            childImageSharp {
              gatsbyImageData(
                placeholder: TRACED_SVG
                width: 250
              )
            }
          }
        }
        desktopLogoFull: logoFull {
          localFile {
            childImageSharp {
              gatsbyImageData(
                placeholder: TRACED_SVG
                width: 393
              )
            }
          }
        }
        desktopLogo: logo {
          localFile {
            childImageSharp {
              gatsbyImageData(
                width: 83
                placeholder: TRACED_SVG
              )
            }
          }
        }
      }
    }
  `)

  const { desktopLogo, desktopLogoFull, logo, logoFull } = strapiGlobal;
  const logoOnlyImages = withArtDirection(getImage(desktopLogo.localFile), [
    {
      media: "(max-width: 767px)",
      image: getImage(logo.localFile),
    },
  ])
  const logoFullImages = withArtDirection(getImage(desktopLogoFull.localFile), [
    {
      media: "(max-width: 767px)",
      image: getImage(logoFull.localFile),
    },
  ])
  const logoImg = logoOnly ? logoOnlyImages : logoFullImages;
  const logoClasses = logoOnly ? `w-[53px] lg:w-[83px]` : `w-[250px] xl:w-[393px]`;

  const [isMobileNavVisible, setIsMobileNavVisible] = useState(false);
  const mobileNavRef = useRef();
  const { t: intl } = useTranslation()
  useOutsideClick(mobileNavRef, (event) => {
    if (event.target.id === "mobileNav") {
      return;
    }

    setIsMobileNavVisible(false);
  });

  return (
    <header className="relative z-10">
      <nav className="container flex flex-wrap flex-row items-center justify-between py-3">
        <Link to="/" className="text-xl font-medium">
          <GatsbyImage className={logoClasses} image={getImage(logoImg)} alt="Logo" />
        </Link>
        <button id="mobileNav" ref={mobileNavRef} onClick={() => setIsMobileNavVisible(!isMobileNavVisible)}>
          <StaticImage
            className="text-xl visible lg:invisible pointer-events-none"
            src="./mobile-menu-icon.png"
            alt="mobile menu bar"
            layout="fixed"
            placeholder="tracedSVG"
          />
        </button>
        <DesktopNav />
      </nav>
      <MobileNav {...{ forwardRef: mobileNavRef, visible: isMobileNavVisible }} />
    </header>
  )
}

export default Navbar
