import { Link, useTranslation } from "gatsby-plugin-react-i18next"
import React from "react"
import { navItems } from "./navbar"

const Footer = () => {
  const currentYear = new Date().getFullYear()
  const { t } = useTranslation()

  const copyrightItemsClasses = [
    'mb-2',
    'md:border-r',
    'md:pr-6',
  ];

  return (
    <footer className="text-white text-center">
      <div className="bg-secondary py-8">
        <div className="container flex flex-wrap justify-center">
          {navItems(t).map(([title, url]) => (
            <Link key={`footer-nav-${url}`} className="text-white hover:text-primary text-lg px-8 py-3" to={url}>
              {title}
            </Link>
          ))}
        </div>
      </div>
      <div className="bg-primary py-5 text-sm">
        <div className="space-x-6 flex flex-col md:flex-row justify-center">
          <span className={copyrightItemsClasses.join(' ')}>
            &copy; Copyright {currentYear}
          </span>
          <span className={copyrightItemsClasses.join(' ')}>
            <Link className="text-white" to="/">
              Advisory Apps Sdn Bhd [1176817-H]
            </Link>
          </span>
          <span>
            {t('All Rights Reserved')}
          </span>
        </div>
      </div>
    </footer>
  )
}

export default Footer
